<script setup lang="ts">
import { ref, watch, computed } from 'vue'
import { ElMessage, ElForm } from 'element-plus'

import {addMerchant, editMerchant, getMerchantDetail} from "@/api/merchant.ts";

const visible = ref(false)
const formRef = ref<InstanceType<typeof ElForm>>()
const loading = ref(false)

const form = ref({
  id: 0,
  login_account: "",
  merchant_name: "",
  status: 1,
  withdraw_config_type: 1,
  withdraw_fee: 0,
  withdraw_rate: 0,
  whitelist_ips: "",
})

// ËÆ°ÁÆóÂ±ûÊÄßÔºöÊ†πÊçÆÈÖçÁΩÆÁ±ªÂûãÂà§Êñ≠ÊòØÂê¶ÊòæÁ§∫ÊèêÁé∞ÊâãÁª≠Ë¥π
const showWithdrawFee = computed(() => {
  return form.value.withdraw_config_type === 2 // ÂçïÁ¨îÂõ∫ÂÆö
})

// ËÆ°ÁÆóÂ±ûÊÄßÔºöÊ†πÊçÆÈÖçÁΩÆÁ±ªÂûãÂà§Êñ≠ÊòØÂê¶ÊòæÁ§∫ÊèêÁé∞Ë¥πÁéá
const showWithdrawRate = computed(() => {
  return form.value.withdraw_config_type === 3 // ÂçïÁ¨îÊØî‰æã
})

// ÈáëÈ¢ùÊ†ºÂºèÂåñÔºöÂàÜËΩ¨ÂÖÉ
const formatAmount = (amount: number) => {
  return (amount / 100).toFixed(2)
}

// ÈáëÈ¢ùÊ†ºÂºèÂåñÔºöÂÖÉËΩ¨ÂàÜ
const parseAmount = (amount: string) => {
  return Math.round(parseFloat(amount) * 100)
}

// Ë¥πÁéáÊ†ºÂºèÂåñÔºöÁôæÂàÜÊØîÊòæÁ§∫
const formatRate = (rate: number) => {
  return (rate / 100).toFixed(2)
}

// Ë¥πÁéáÊ†ºÂºèÂåñÔºöÁôæÂàÜÊØîËΩ¨Â≠òÂÇ®Ê†ºÂºè
const parseRate = (rate: string) => {
  return Math.round(parseFloat(rate) * 100)
}

const emit = defineEmits<{
  (event: 'refresh'): void
}>()

// ÂÆö‰πâËßÑÂàô
const rules = ref({
  login_account: [{ required: true, message: 'ËØ∑ËæìÂÖ•ÁôªÂΩïË¥¶Âè∑', trigger: 'blur' }],
  merchant_name: [{ required: true, message: 'ËØ∑ËæìÂÖ•ÂïÜÊà∑ÂêçÁß∞', trigger: 'blur' }],
  withdraw_config_type: [{ required: true, message: 'ËØ∑ÈÄâÊã©ÊèêÁé∞ÈÖçÁΩÆÁ±ªÂûã', trigger: 'change' }],
  withdraw_fee: [
    {
      required: false,
      message: 'ËØ∑ËæìÂÖ•ÊèêÁé∞ÊâãÁª≠Ë¥π',
      trigger: 'blur',
      validator: (_rule: any, value: any, callback: any) => {
        if (form.value.withdraw_config_type === 2) {
          if (!value || isNaN(Number(value)) || Number(value) < 0) {
            callback(new Error('ËØ∑ËæìÂÖ•ÊúâÊïàÁöÑÊèêÁé∞ÊâãÁª≠Ë¥π'))
          } else {
            callback()
          }
        } else {
          callback()
        }
      }
    }
  ],
  withdraw_rate: [
    {
      required: false,
      message: 'ËØ∑ËæìÂÖ•ÊèêÁé∞Ë¥πÁéá',
      trigger: 'blur',
      validator: (_rule: any, value: any, callback: any) => {
        if (form.value.withdraw_config_type === 3) {
          if (!value || isNaN(Number(value)) || Number(value) < 0) {
            callback(new Error('ËØ∑ËæìÂÖ•ÊúâÊïàÁöÑÊèêÁé∞Ë¥πÁéá'))
          } else {
            callback()
          }
        } else {
          callback()
        }
      }
    }
  ],
})

const open = async (data: any = {}) => {
  console.log('üîç EditForm open called', { 
    data, 
    visible: visible.value,
    loading: loading.value 
  });
  
  // ÂÖàÈáçÁΩÆË°®Âçï
  resetForm()
  loading.value = true
  
  try {
    // Á°Æ‰øù data ‰∏ç‰∏∫ null Êàñ undefined
    const safeData = data || {}
    let merged: any = { ...safeData }
    
    // Ê£ÄÊü•ÊòØÂê¶ÊúâÊúâÊïàÁöÑ id ‰∏îÂ§ß‰∫é 0
    if (safeData && safeData.id && typeof safeData.id === 'number' && safeData.id > 0) {
      // ÁºñËæëÊ®°ÂºèÔºö‰ªéËØ¶ÊÉÖËé∑ÂèñÊï∞ÊçÆ
      try {
        const res = await getMerchantDetail('/api/v1/admin/merchant/detail', safeData.id)
        if (res.status && res.data) {
          merged = { ...res.data }
        } else {
          ElMessage.error('Ëé∑ÂèñÂïÜÊà∑ËØ¶ÊÉÖÂ§±Ë¥•')
          return
        }
      } catch (error) {
        ElMessage.error('Ëé∑ÂèñÂïÜÊà∑ËØ¶ÊÉÖÂ§±Ë¥•')
        return
      }
    }

    // Â§ÑÁêÜÈáëÈ¢ùÂ≠óÊÆµÔºöÂàÜËΩ¨ÂÖÉÊòæÁ§∫Ôºà‰ªÖÂú®ÁºñËæëÊ®°Âºè‰∏ãÔºâ
    if (safeData && safeData.id && typeof safeData.id === 'number' && safeData.id > 0) {
      if (merged.withdraw_fee) {
        merged.withdraw_fee = formatAmount(merged.withdraw_fee)
      }
      // Â§ÑÁêÜË¥πÁéáÂ≠óÊÆµÔºöËΩ¨Êç¢‰∏∫ÁôæÂàÜÊØîÊòæÁ§∫
      if (merged.withdraw_rate) {
        merged.withdraw_rate = formatRate(merged.withdraw_rate)
      }
    }

    // Âè™Êõ¥Êñ∞ÊúâÂÄºÁöÑÂ≠óÊÆµ
    Object.keys(merged).forEach(key => {
      if (merged[key] !== undefined && merged[key] !== null && merged[key] !== '') {
        (form.value as any)[key] = merged[key]
      }
    })
    
    // Â¶ÇÊûúÊòØÊñ∞Â¢ûÊ®°ÂºèÔºåÁ°Æ‰øùË°®ÂçïÊúâÊ≠£Á°ÆÁöÑÈªòËÆ§ÂÄº
    if (!safeData || !safeData.id || safeData.id <= 0) {
      form.value.id = 0
      form.value.status = 1
      form.value.withdraw_fee = 0
      form.value.withdraw_rate = 0
      form.value.withdraw_config_type = 1
      form.value.whitelist_ips = ""
    }
    visible.value = true
  } catch (error) {
    ElMessage.error('Âä†ËΩΩÊï∞ÊçÆÂ§±Ë¥•')
  } finally {
    loading.value = false
  }
}

const handleSubmit = async () => {
  try {
    await formRef.value?.validate()

    // ÂáÜÂ§áÊèê‰∫§Êï∞ÊçÆ
    const submitData = { ...form.value }
    
    // Ê†πÊçÆÈÖçÁΩÆÁ±ªÂûãÂ§ÑÁêÜÁõ∏Â∫îÂ≠óÊÆµ
    if (form.value.withdraw_config_type === 2) {
      // ÂçïÁ¨îÂõ∫ÂÆöÔºöÂ§ÑÁêÜÊèêÁé∞ÊâãÁª≠Ë¥π
      submitData.withdraw_fee = parseAmount(submitData.withdraw_fee.toString())
      submitData.withdraw_rate = 0 // Ë¥πÁéáËÆæ‰∏∫0
    } else if (form.value.withdraw_config_type === 3) {
      // ÂçïÁ¨îÊØî‰æãÔºöÂ§ÑÁêÜÊèêÁé∞Ë¥πÁéá
      submitData.withdraw_rate = parseRate(submitData.withdraw_rate.toString())
      submitData.withdraw_fee = 0 // ÊâãÁª≠Ë¥πËÆæ‰∏∫0
    } else {
      // Á≥ªÁªüËßÑÂàôÔºöÈÉΩËÆæ‰∏∫0
      submitData.withdraw_fee = 0
      submitData.withdraw_rate = 0
    }

    loading.value = true
    let res: any
    if (form.value.id > 0) {
      res = await editMerchant('/api/v1/admin/merchant/edit', submitData)
    } else {
      res = await addMerchant('/api/v1/admin/merchant/add', submitData)
    }

    if (res.status) {
      ElMessage.success(res.message || 'Êìç‰ΩúÊàêÂäü')
      emit('refresh')
      visible.value = false
    } else {
      ElMessage.error(res.message || 'Êìç‰ΩúÂ§±Ë¥•')
    }
  } catch (error) {
    ElMessage.error('Êèê‰∫§Â§±Ë¥•ÔºåËØ∑Ê£ÄÊü•Ë°®Âçï‰ø°ÊÅØ')
  } finally {
    loading.value = false
  }
}

// ÈáçÁΩÆË°®ÂçïÊï∞ÊçÆ
const resetForm = () => {
  form.value = {
    id: 0,
    login_account: "",
    merchant_name: "",
    status: 1,
    withdraw_config_type: 1,
    withdraw_fee: 0,
    withdraw_rate: 0,
    whitelist_ips: "",
  }
}

// ÁõëÂê¨ visible ÂèòÂåñÔºåÂÖ≥Èó≠Êó∂ÈáçÁΩÆË°®Âçï
watch(visible, (newVal, oldVal) => {
  // Âè™ÊúâÂú®‰ªé true Âèò‰∏∫ false Êó∂ÊâçÈáçÁΩÆË°®Âçï
  if (oldVal === true && newVal === false) {
    resetForm()
  }
})

defineExpose({ open })
</script>

<template>
  <el-drawer v-model="visible" :title="form.id > 0 ? 'ÁºñËæëÂïÜÊà∑' : 'Êñ∞Â¢ûÂïÜÊà∑'" size="50%">
    <div v-loading="loading" element-loading-text="Âä†ËΩΩ‰∏≠...">
      <el-form :model="form" :rules="rules" ref="formRef" label-width="150px">
        <!-- Âü∫Êú¨‰ø°ÊÅØ -->
        <el-divider content-position="left">Âü∫Êú¨‰ø°ÊÅØ</el-divider>
        <el-form-item label="ÁôªÂΩïË¥¶Âè∑" prop="login_account">
          <el-input v-model="form.login_account" placeholder="ËØ∑ËæìÂÖ•ÁôªÂΩïË¥¶Âè∑" />
        </el-form-item>
        
        <el-form-item label="ÂïÜÊà∑ÂêçÁß∞" prop="merchant_name">
          <el-input v-model="form.merchant_name" placeholder="ËØ∑ËæìÂÖ•ÂïÜÊà∑ÂêçÁß∞" />
        </el-form-item>
        
        <el-form-item label="Áä∂ÊÄÅ" prop="status">
          <el-switch v-model="form.status" :active-value="1" :inactive-value="0" />
        </el-form-item>

        <!-- ÊèêÁé∞ÈÖçÁΩÆ -->
        <el-divider content-position="left">ÊèêÁé∞ÈÖçÁΩÆ</el-divider>
        <el-form-item label="ÈÖçÁΩÆÁ±ªÂûã" prop="withdraw_config_type">
          <el-select v-model="form.withdraw_config_type" placeholder="ËØ∑ÈÄâÊã©ÈÖçÁΩÆÁ±ªÂûã" style="width: 100%">
            <el-option label="Á≥ªÁªüËßÑÂàô" :value="1" />
            <el-option label="ÂçïÁ¨îÂõ∫ÂÆö" :value="2" />
            <el-option label="ÂçïÁ¨îÊØî‰æã" :value="3" />
          </el-select>
        </el-form-item>

        <!-- Ê†πÊçÆÈÖçÁΩÆÁ±ªÂûãÂä®ÊÄÅÊòæÁ§∫ËæìÂÖ•Ê°Ü -->
        <el-form-item 
          v-if="showWithdrawFee" 
          label="ÊèêÁé∞ÊâãÁª≠Ë¥π" 
          prop="withdraw_fee"
        >
          <el-input v-model="form.withdraw_fee" placeholder="Âçï‰ΩçÔºöÂÖÉ">
            <template #append>ÂÖÉ</template>
          </el-input>
        </el-form-item>

        <el-form-item 
          v-if="showWithdrawRate" 
          label="ÊèêÁé∞Ë¥πÁéá" 
          prop="withdraw_rate"
        >
          <el-input v-model="form.withdraw_rate" placeholder="‰æãÂ¶ÇÔºö1.5Ë°®Á§∫1.5%">
            <template #append>%</template>
          </el-input>
        </el-form-item>

        <!-- ÁôΩÂêçÂçïIP -->
        <el-form-item label="ÁôΩÂêçÂçïIP">
          <el-input 
            v-model="form.whitelist_ips" 
            type="textarea" 
            placeholder="ËØ∑ËæìÂÖ•IPÂú∞ÂùÄÔºåÂ§ö‰∏™IPÁî®|ÂàÜÈöîÔºå‰æãÂ¶ÇÔºö192.168.1.1|192.168.1.2|10.0.0.1"
            :rows="3 as number"
          />
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click="handleSubmit" :loading="loading">Êèê‰∫§</el-button>
          <el-button @click="visible = false">ÂèñÊ∂à</el-button>
        </el-form-item>
      </el-form>
    </div>
  </el-drawer>
</template>

<style scoped>
.el-form-item {
  margin-bottom: 20px;
}

